@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.App.dark {
  background-color: #111;
  color: white;
}

.App-header {
  background-color: #09347a;
  color: aliceblue;
  width: 100%;
  height: 10vh;
  font-size: 40px;
  padding: 2px;
  text-shadow: 2px 0 0 #609dff, -2px 0 0 #609dff, 0 2px 0 #609dff,
    0 -2px 0 #609dff, 1px 1px #609dff;
  text-align: center;
}

.stat {
  font-size: 0.8rem;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 500;
}

.stat-container {
  font-size: 0.7rem;
  border-radius: 1rem;
  width: 24rem;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
}

.toggle-theme {
  position: fixed;
  top: 5rem;
  right: 2em;
}

.card.dark {
  background-color: #222;
}

.stat-container.dark {
  background-color: #222;
}

input.dark {
  background-color: #222;
  color: white;
  border: "5px solid white";
}

